@-webkit-keyframes flip-scale-up-hor {
  0% {
    -webkit-transform: scale(1) rotateX(0);
    transform: scale(1) rotateX(0); }
  25% {
    -webkit-transform: scale(1.2) rotateX(-90deg);
    transform: scale(1.2) rotateX(-90deg); }
  50% {
    -webkit-transform: scale(1.4) rotateX(-180deg);
    transform: scale(1.4) rotateX(-180deg); }
  75% {
    -webkit-transform: scale(1.2) rotateX(-270deg);
    transform: scale(1.2) rotateX(-270deg); }
  100% {
    -webkit-transform: scale(1) rotateX(-360deg);
    transform: scale(1) rotateX(-360deg); } }

@keyframes flip-scale-up-hor {
  0% {
    -webkit-transform: scale(1) rotateX(0);
    transform: scale(1) rotateX(0); }
  25% {
    -webkit-transform: scale(1.2) rotateX(-90deg);
    transform: scale(1.2) rotateX(-90deg); }
  50% {
    -webkit-transform: scale(1.4) rotateX(-180deg);
    transform: scale(1.4) rotateX(-180deg); }
  75% {
    -webkit-transform: scale(1.2) rotateX(-270deg);
    transform: scale(1.2) rotateX(-270deg); }
  100% {
    -webkit-transform: scale(1) rotateX(-360deg);
    transform: scale(1) rotateX(-360deg); } }

